.row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: (-$gap-s);
  margin-right: (-$gap-s);
  &> [class^="col-"] {
  	padding-left: $gap-s;
  	padding-right: $gap-s;
  }
  &.narrow{
  	margin-left: (-$gap-s/2);
  	margin-right: (-$gap-s/2);
  	&> [class^="col-"] {
	  	padding-left: $gap-s/2;
	  	padding-right: $gap-s/2;
	  }
  }
  &.no-gap{
  	margin-left: 0;
  	margin-right: 0;
  	&> [class^="col-"] {
	  	padding-left: 0;
	  	padding-right: 0;
	  }
  }
  &.reverse {
    flex-direction: row-reverse;
  }
  &.col--center{
  	justify-content: center;
  }
  &.col--between{
  	justify-content: space-between;
  }
  &.col--right{
  	justify-content: flex-end;
  }
  &.equal-height {
    &> [class^="col-"] {
      display: flex;
    }
  }
  &.fixed-flex{
  	flex-wrap: nowrap;
  	.col-fixed{
  		flex: 0 0 auto;
  	}
  	.col-flex{
  		flex: 9999 1 auto;
  	}
  }
}

